                  {id:'33', cat:'c6',img:['products/Handgrip-LOGO.jpg', 'products/Handgrip-er2.jpg'], title:'HG135, Handheld Grip', description:'<p >GS HG135,  Handgrip</p>  <p>GS HG135, hand grip used for GS Componion series barcode scanners.  M100BT, M300BT (pro) and M500BT can be fixed on HG135,  used as traditional gun-type scanners.  A 800mAh  rechargeable battery is equipped with HG135，used for  charging barcode scanners, providing an extra power supply. </p>  <p>HG135 can be switched to USB mode when an  external USB cable is plugged onto hand grip, scanner can be trigged using the button on the bottom. </p>  <p>Detailed specification download </p>  <p>STD135, Stand for Handgrip </p><p>Stand for companion series barcode scanners</p>'},
                  {id:'34', cat:'c6',img:['products/HM8000_l.jpg','products/HM8000.jpg'], title:'Helmet Scanner', description:'<p>Generalscan head-wearing barcode scanner, GS HM8000,  is the newest generation wearable barcode scanner builds on most popular  GS mini barcode scanners. Integrated with Bluetooth 4.0 technology, supports HID, SPP, BLE, Master&amp;Slave  modes, compatible with iOS, Android and Windows devices. It is a comfortable  and easy-to-implement productivity tool for all mobile operators who regularly  need to scan barcodes.</p>'},
                  {id:'35', cat:'c6',img:['products/PB200_l.jpg','products/PB200a.jpg'], title:'PB200, Power Bank', description:'<p>GS PB200 Power Bank</p>  <ul>    <li>Dimention 256x35x11 mm</li>    <li> Capacity 2200 mAh</li><li> Weight 90g<br />  </ul>'},
                  // {id:'36', img:['products/map.jpg','products/red-leaves.jpg'], title:'product product product', description:'product wtrwtg kjldfjg sgg gdhs hshs hhshf hfshh hhfssaaag hshfjcbm'},
              ],
        }
      },
        computed:{
            getProduct: function () {
              return this.products.find((el)=>{return el.id===this.id})
            },
    },
    }
