<template>
   <div>
       <section>
    <v-parallax src="/static/technique.jpg" height="180">
      <v-layout
        column
        align-center
        justify-center
        class="white--text"
      >
        <h1 class="white--text mb-2 display-1 text-xs-center">CONTACT US</h1>
        <div class="subheading mb-3 text-xs-center">Mobile Data Acquisition and Processing</div>
      </v-layout>
    </v-parallax>
  </section>
 <section id="contact" class="indigo darken-3">
    <v-layout row wrap>
      <v-flex xs12 md6>
        <v-layout
          column
          justify-center
          class="mt-5"
        >
          <v-flex xs12>
            <v-layout
            column
            justify-center
            class="white--text mt-2">
              <v-flex xs7 offset-xs3>
               <v-btn color="white" flat class="text-xs-left">
                <v-icon left >home</v-icon>Fischreiherstraße 3<br>76187 Karlsruhe Germany
              </v-btn>
              </v-flex>
              <v-flex xs7 offset-xs3>
               <v-btn color="white" flat >
                  <v-icon dark left>phone</v-icon>+49 721 97688427
                </v-btn>
              </v-flex>
              <v-flex xs7 offset-xs3>
                <v-btn color="white" flat
                  href="mailto:info@karls-tech.com?Subject=Requirment">
                  <v-icon dark left>email</v-icon>info@karls-tech.com
                </v-btn>
              </v-flex>
              <v-flex xs7 offset-xs3>
                <v-btn color="white" flat to="/">
                  <v-icon dark left>contacts</v-icon>www.karls-tech.com
                </v-btn>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex xs12 md6>
       <gmap-map
        :center="center"
        :zoom="17"
        style="height: 300px"
      >
          <gmap-marker
            :key="index"
            v-for="(m, index) in markers"
            :position="m.position"
            :clickable="true"
            :draggable="true"
            @click="center=m.position"
          >
          </gmap-marker>
        </gmap-map>
      </v-flex>

    </v-layout>
  </section>
  </div>
</template>

<script>
    export default {
        name: "contact",
        data(){
          return{
            center: {lat: 49.043883799999996, lng: 8.366834200000005},
            markers: [
              {position: {lat: 49.043883799999996, lng: 8.366834200000005}},

              ]
          }
        }
    }
</script>

<style scoped>

</style>



