              {id:'1', icon:'data_usage', img:['solution/APP-PDA.jpg'],title:'General Mobile Data Acquistic', description:'<ul><li>Recording shots of current count / materials / spare parts as proof></li><li>Storing individual shots neatly under Customer / Order / Date</li><li>Calculation of consumption figures / costs</li><li>Verification of stock information on site</li><li>Ordering materials / spare parts without delay</li><li>Creating Ablesebelegen / invoices on site</li><li>Creating reports by a mouse click</li><li>Collection static information from databases by configurable search criteria</li><p><br>Hardware support:<br/>Barcode scanner, PDA, Tablet, Printer, etc.Communication: 1D/2D/RFID/WLan/Bluetooth/etc<p/></ul>'},
