<template>
  <v-app>
    <v-navigation-drawer
      fixed
      temporary
      :mini-variant="miniVariant"
      :clipped="clipped"
      v-model="drawer"
      :right="right"
      app
    >
      <v-list>
        <v-list-tile
          value="true"
          v-for="(item, i) in items"
          v-if="item.title!=='Shop'"
          :key="i"
          :to="item.link"
          :exact="item.lexact"
        >
          <v-list-tile-action>
            <v-icon v-html="item.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile
          value="true"
          v-else
          :key="i"
          :href="item.link"
          :exact="item.lexact"
          target="_blank"
        >
          <v-list-tile-action>
            <v-icon v-html="item.icon"></v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title"></v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar fixed app :clipped-left="clipped">
      <router-link to="/"><img src="/static/logo.png" alt="Vuetify.js" class=""  height="50"/></router-link>
      <router-link to="/"><v-toolbar-title v-text="title" ></v-toolbar-title></router-link>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn
          flat
          v-for="(item, i) in items"
          v-if="item.title!=='Shop'"
          :key="i"
          :to="item.link"
          :exact="item.lexact"
        >
          <v-icon left v-html="item.icon"></v-icon>
          {{ item.title }}
        </v-btn>
        <v-btn
          flat
          v-else
          :key="i"
          :href="item.link"
          :exact="item.lexact"
          target="_blank"
        >
          <v-icon left v-html="item.icon"></v-icon>
          {{ item.title }}
        </v-btn>
      </v-toolbar-items>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
    </v-toolbar>
    <v-content>
       <router-view></router-view>
    </v-content>
    <v-footer :fixed="fixed" app>
      <span>&copy; 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
  export default {
    data () {
      return {
        clipped: false,
        drawer: false,
        fixed: false,
        items: [{
          icon: 'home',
          title: 'Home',
          link: '/',
          lexact: true
        },
        {
          icon: 'explore',
          title: 'Solution',
          link: '/solution',
          lexact: false
         },
         {
          icon: 'developer_board',
          title: 'Product',
          link: '/product',
          lexact: false
         },
         {
          icon: 'apps',
          title: 'App',
          link: '/app',
           lexact: false
         },
          {
          icon: 'shopping_cart',
          title: 'Shop',
          link: 'http://karls-tech.com/eshop/',
          lexact: false
         },
         {
          icon: 'contacts',
          title: 'Contact',
          link: '/contact',
          lexact: false
         }
        ],
        miniVariant: false,
        right: true,
        rightDrawer: false,
        title: 'Karls Tech'
      }
    }
  }
</script>



